<template>
    <section class="content">
        <add name="Access-Control-Allow-Origin" value="*" />
 <add name="Access-Control-Allow-Methods" value="GET, POST" />
    <div class="row center-block">
        <div class="col-md-12">
            <div class="box">
                <div class="box-header">
                    <h3 class="box-title">Novo Produto</h3>
                </div>
                <div class="box-body">
                    <div class="row">
                        <div class="col-md-6">
                            <h4>Descrio</h4>
                            <input v-model="descricao" type="text" class="form-control" >
                        </div>
                        <div class="col-md-6">
                            <h4>Complemento</h4>
                            <textarea v-model="complemento" rows="1" cols="12" class="form-control"></textarea>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-3">
                            <h4>Preo</h4>
                            <input v-model="preco" type="number" value="0" min="0" step="0.01" data-number-to-fixed="2" data-number-stepfactor="100" class="form-control currency"/>
                        </div>
                        <div class="col-md-3">
                            <h4>Categoria</h4>
                            <select class="form-control">
                                <option v-for="c in categories" v-model="categoria_selecionada">{{c.id_categoria}} - {{c.categoria}}</option>
                            </select>
                        </div>
                        <div class="col-md-6">
                            <h4>Link da Imagem </h4>
                            <input type="text" class="form-control" v-model="imagem">
                        </div>
                    </div>
                    <br><br>
                    <button class="btn" @click="gravar">Gravar</button>
                    <button class="btn">Cancelar</button>
                </div>
            </div>
        </div>
    </div>
    </section>
</template>
<script>
import axios from 'axios'

export default {
  name: 'NovoProduto',
  data () {
    return {
      descricao: '',
      categories: [],
      complemento: '',
      categoria_selecionada: '',
      preco: 0,
      imagem: ''
    }
  },
  created () {
    this.getCategories()
  },
  methods: {
    getCategories () {
      let url = 'https://sev-delivery.herokuapp.com/categories'
      axios.get(url).then(response => {
        this.categories = response.data
        console.log(this.categories)
      })
    },
    gravar () {
      console.log(this.descricao)
      console.log(this.complemento)
      console.log(this.categoria_selecionada)
      console.log(this.preco)
      console.log(this.imagem)
    }
  }
}
</script>



