<template>
    <section class="content">
    <div class="row center-block">
        <div class="col-md-12">
            <div class="box">
            <div class="box-header">
                <h3 class="box-title">Data Table With Full Features</h3>
            </div>
            <!-- /.box-header -->
            <div class="box-body">
                <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
                <div class="row">
                    <div class="col-sm-6">
                    <div id="example1_length" class="dataTables_length">

                    </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-sm-12 table-responsive">
                    <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                        <thead>
                        <tr>
                            <th v-for="coluna in colunas">
                                <a @click="sortBy(coluna)">{{coluna | ucwords}}</a>
                            </th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr class="even" role="row" v-for="u in usersFiltered">
                            <td>{{u.nome}}</td>
                            <td>{{u.sobrenome}}</td>
                            <td>{{u.foto_perfil}}</td>
                            <td>{{u.genero}}</td>
                            <td>{{u.fb_id}}</td> 
                        </tr>
                                               
                        </tbody>
                        <tfoot>
                        <tr>
                            <th colspan="1" rowspan="1">Nome</th>
                            <th colspan="1" rowspan="1">Sobrenome</th>
                            <th colspan="1" rowspan="1">Foto do Perfil</th>
                            <th colspan="1" rowspan="1">Genero</th>
                            <th colspan="1" rowspan="1">Facebook ID</th>
                        </tr>
                        </tfoot>
                    </table>
                    </div>
                </div>
                </div>
                <!-- /.box-body -->
            </div>
            </div>
        </div>
        </div>
    </section>
</template>

<script>

// import $ from 'jquery'
import axios from 'axios'
import _ from 'lodash'
// Require needed datatables modules
// import 'datatables.net'
// import 'datatables.net-bs'

export default {
  name: 'Tables',
  data () {
    return {
      order: {
        keys: ['nome', 'sobrenome', 'foto', 'genero', 'facebook id'],
        sort: ['desc', 'desc', 'desc', 'desc', 'desc']
      },
      users: [],
      colunas: ['nome', 'sobrenome', 'foto', 'genero', 'facebook id']
    }
  },
  created () {
    this.getUsers()
  },
  methods: {
    getUsers () {
      let url = 'https://sev-delivery.herokuapp.com/clientes'
      axios.get(url).then(response => {
        this.users = response.data
      })
    },
    sortBy (coluna) {
      this.order.keys = 'coluna'
      this.order.sort = this.order.sort === 'desc' ? 'asc' : 'desc'
    }
  },
  filters: {
    ucwords (value) {
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  computed: {
    usersFiltered () {
      return _.orderBy(this.users, this.order.keys, this.order.sort)
    }
  }
}
</script>
<style>

</style>



