<template>
    <section class="content">
    <div class="row center-block">
        <div class="col-md-12">
            <div class="box">
                <div class="box-header">
                    <h3 class="box-title">Pesquisar</h3>
                </div>
                <div class="box-body">
                    <div class="row">
                        <div class="col-md-1">
                        <h4>Nome</h4>
                        </div>
                        <div class="col-md-11">
                        <input class="form-control" placeholder="Nome do produto ..." type="text">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-1">
                        <h4>Categoria</h4>
                        </div>
                        <div class="col-md-9">
                        <div class="form-group">
                            <select class="form-control" multiple="" >
                            <option>option 1</option>
                            <option>option 2</option>
                            <option>option 3</option>
                            <option>option 4</option>
                            <option>option 5</option>
                            </select>
                        </div>
                        </div>
                        <div class="col-md-2">
                            
                            <button class="btn">Pesquisar</button>
                            <br>
                            <br>
                            <router-link class="pageLink" to="/novo-produto">
                            <button class="btn" >Novo Produto</button>
                            </router-link>
                        </div>
                    </div>

                  
                </div>
            </div>
        </div>       

        <div class="col-md-12">
            <div class="box">
            <div class="box-header">
                <h3 class="box-title">Data Table With Full Features</h3>
            </div>
            <!-- /.box-header -->
            <div class="box-body">
                <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
                <div class="row">
                    <div class="col-sm-6">
                    <div id="example1_length" class="dataTables_length">

                    </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-sm-12 table-responsive">
                    <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                        <thead>
                        <tr role="row">
                            <th aria-label="Nome: activate to sort column descending" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">Nome</th>
                            <th aria-label="Descricao: activate to sort column ascending" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Descrio</th>
                            <th aria-label="Preo: activate to sort column ascending" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Preo</th>
                            <th aria-label="Categoria: activate to sort column ascending" style="width: 142px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Categoria</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr class="even" role="row" v-for="p in products">
                            <td class="sorting_1">{{p.name}}</td>
                            <td>{{p.description}}</td>
                            <td>{{p.price}}</td>
                            <td>{{p.categoria}}</td>
                        </tr>
                                               
                        </tbody>
                        <tfoot>
                        <tr>
                            <th colspan="1" rowspan="1">Nome</th>
                            <th colspan="1" rowspan="1">Descrio</th>
                            <th colspan="1" rowspan="1">Preo</th>
                            <th colspan="1" rowspan="1">Categoria</th>
                     
                        </tr>
                        </tfoot>
                    </table>
                    </div>
                </div>
                </div>
                <!-- /.box-body -->
            </div>
            </div>
        </div>
        </div>
    </section>
</template>

<script>
import axios from 'axios'
import VueRouter from 'vue-router'
// import routes from '../../routes.js'

var router = new VueRouter()

export default {
  name: 'Tables',
  data () {
    return {
      products: []
    }
  },
  created () {
    this.getProducts()
  },
  methods: {
    getProducts () {
      let url = 'https://sev-delivery.herokuapp.com/products'
      axios.get(url).then(response => {
        this.products = response.data
      })
    },
    pageNewProduct () {
      console.log('new product')
      router.go({name: 'Clientes'})
    }
  }
}
</script>
<style>

</style>



